import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppContext } from '../../context/AppContext';
import {
  Box,
  Button,
  Container,
  Paper,
  TextField,
  Typography,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent,
} from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';

const LoginPage: React.FC = () => {
  const { classes } = useAppContext();
  const navigate = useNavigate();
  
  const [selectedClass, setSelectedClass] = useState('');
  const [selectedStudent, setSelectedStudent] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  
  // Get student IDs from the selected class
  const classStudents = selectedClass 
    ? classes.find(c => c.id === selectedClass)?.students || [] 
    : [];

  const handleClassChange = (event: SelectChangeEvent) => {
    setSelectedClass(event.target.value);
    setSelectedStudent(''); // Reset student selection when class changes
  };

  const handleStudentChange = (event: SelectChangeEvent) => {
    setSelectedStudent(event.target.value);
  };

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    setError('');

    // Simple validation logic
    if (!selectedClass || !selectedStudent) {
      setError('Please select both class and student');
      return;
    }

    // In a real app, you'd verify the password against a secure backend
    // For this demo, we'll use a simple password check
    // NOTE: This is NOT secure for a production app!
    const defaultPassword = 'foxbox'; // Simple default password
    if (password !== defaultPassword) {
      setError('Incorrect password');
      return;
    }

    // Store login info in localStorage for persistence
    localStorage.setItem('currentStudent', selectedStudent);
    localStorage.setItem('currentClass', selectedClass);
    
    // Navigate to student study page
    navigate(`/student/${selectedStudent}/study`);
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 8 }}>
      <Paper
        elevation={3}
        sx={{
          p: 4,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          background: 'rgba(30, 30, 30, 0.9)',
          borderRadius: 2,
        }}
      >
        <Box
          sx={{
            bgcolor: 'primary.main',
            color: 'background.default',
            borderRadius: '50%',
            p: 1,
            mb: 2,
          }}
        >
          <LockOutlinedIcon />
        </Box>
        <Typography component="h1" variant="h5" sx={{ mb: 3 }}>
          FoxBox Student Login
        </Typography>

        {error && (
          <Alert severity="error" sx={{ mb: 2, width: '100%' }}>
            {error}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} sx={{ width: '100%' }}>
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel id="class-select-label">Class</InputLabel>
            <Select
              labelId="class-select-label"
              id="class-select"
              value={selectedClass}
              label="Class"
              onChange={handleClassChange}
              required
            >
              {classes.map((cls) => (
                <MenuItem key={cls.id} value={cls.id}>
                  {cls.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel id="student-select-label">Student</InputLabel>
            <Select
              labelId="student-select-label"
              id="student-select"
              value={selectedStudent}
              label="Student"
              onChange={handleStudentChange}
              disabled={!selectedClass}
              required
            >
              {classStudents.map((student) => (
                <MenuItem key={student.id} value={student.id}>
                  {student.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            sx={{ mb: 2 }}
          />

          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 2, mb: 2, py: 1.5 }}
          >
            Sign In
          </Button>
          
          <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 2 }}>
            Access your flashcards and continue learning.
          </Typography>
        </Box>
      </Paper>
    </Container>
  );
};

export default LoginPage; 