import React, { useEffect, useState } from 'react';
import { AppBar, Toolbar, Typography, Button, Box, Tooltip } from '@mui/material';
import { useNavigate, Link, useLocation } from 'react-router-dom';
import SchoolIcon from '@mui/icons-material/School';
import PersonIcon from '@mui/icons-material/Person';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
// import MenuBookIcon from '@mui/icons-material/MenuBook'; // Removed unused import
import logo from '../../fox-logo.png';
import { useAppContext } from '../../context/AppContext';

const AppHeader: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { classes } = useAppContext();
  const [isStudent, setIsStudent] = useState(false);
  const [studentName, setStudentName] = useState('');

  useEffect(() => {
    // Check if there's a logged-in student
    const currentStudentId = localStorage.getItem('currentStudent');
    
    if (currentStudentId) {
      setIsStudent(true);
      
      // Find student name
      const currentClassId = localStorage.getItem('currentClass');
      if (currentClassId) {
        const currentClass = classes.find(c => c.id === currentClassId);
        if (currentClass) {
          const student = currentClass.students.find(s => s.id === currentStudentId);
          if (student) {
            setStudentName(student.name);
          }
        }
      }
    } else {
      setIsStudent(false);
      setStudentName('');
    }
  }, [classes, location.pathname]); // Re-check when location changes

  const handleLogout = () => {
    localStorage.removeItem('currentStudent');
    localStorage.removeItem('currentClass');
    setIsStudent(false);
    navigate('/login');
  };

  return (
    <AppBar position="static" elevation={4} sx={{ backgroundColor: 'background.paper' }}>
      <Toolbar>
        <Link to={isStudent ? `/student/${localStorage.getItem('currentStudent')}/study` : "/teacher"} 
          style={{ textDecoration: 'none', color: 'inherit', display: 'flex', alignItems: 'center' }}>
          <img src={logo} alt="FoxBox Logo" style={{ height: '60px', marginRight: '15px' }} />
          <Typography variant="h6" component="div" sx={{ display: { xs: 'none', sm: 'block' } }}>
            FoxBox Leitner
          </Typography>
        </Link>
        <Box sx={{ flexGrow: 1 }} />
        
        {isStudent ? (
          // Student navigation
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography sx={{ mr: 2 }}>
              {studentName}
            </Typography>
            <Tooltip title="Sign Out">
              <Button
                color="inherit"
                onClick={handleLogout}
                startIcon={<ExitToAppIcon />}
              >
                Sign Out
              </Button>
            </Tooltip>
          </Box>
        ) : (
          // Teacher/Login navigation
          <Box>
            {location.pathname !== '/login' && (
              <Tooltip title="Teacher Dashboard">
                <Button
                  color="inherit"
                  onClick={() => navigate('/teacher')}
                  startIcon={<SchoolIcon />}
                >
                  Dashboard
                </Button>
              </Tooltip>
            )}
            
            {location.pathname === '/teacher' && (
              <Tooltip title="Student Login">
                <Button
                  color="inherit"
                  onClick={() => navigate('/login')}
                  startIcon={<PersonIcon />}
                >
                  Student Login
                </Button>
              </Tooltip>
            )}
          </Box>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default AppHeader; 